// Legges i variables

// Light
$ske-light-primary-button-color: #328279;
$ske-light-primary-button-color-contrast: #FFFFFF;


.mdlext-light-color-theme {
  background-color: $ske-light-content-background-color;
  color: $ske-light-text-color-primary;
}

// mdl/src/palette/_palette.scss
// -----------------------------
.mdlext-light-color-theme {

  // Material Design Lite

  .mdl-color--primary {
    background-color: $ske-light-color-primary !important;
  }

  .mdl-color--primary-contrast {
    background-color: $ske-light-color-primary-contrast !important;
  }

  .mdl-color--primary-dark {
    background-color: $ske-light-color-primary-dark !important;
  }

  .mdl-color--accent {
    background-color: $ske-light-color-accent !important;
  }

  .mdl-color--accent-contrast {
    background-color: $ske-light-color-accent-contrast !important;
  }

  .mdl-color-text--primary {
    color: $ske-light-color-primary !important;
  }

  .mdl-color-text--primary-contrast {
    color: $ske-light-color-primary-contrast !important;
  }

  .mdl-color-text--primary-dark {
    color: $ske-light-color-primary-dark !important;
  }

  .mdl-color-text--accent {
    color: $ske-light-color-accent !important;
  }

  .mdl-color-text--accent-contrast {
    color: $ske-light-color-accent-contrast !important;
  }


  // SKE

  .ske-color--primary-button {
    background-color: $ske-light-primary-button-color;
  }

  .ske-color--primary-button-contrast {
    background-color: $ske-light-primary-button-color-contrast;
  }

  .ske-color--secondary {
    background-color: $ske-light-secondary-color;
  }

  .ske-color--secondary-contrast {
    background-color: $ske-light-secondary-color-contrast;
  }

  .ske-color--imagearea {
    background-color: $ske-light-imagearea-color;
  }

  .ske-color--imagearea-contrast {
    background-color: $ske-light-imagearea-color-contrast;
  }

  .ske-color--error {
    background-color: $ske-light-error-color;
  }

  .ske-color--error-contrast {
    background-color: $ske-light-error-color-contrast;
  }

  .ske-color-text--primary-button {
    color: $ske-light-primary-button-color;
  }

  .ske-color-text--primary-button-contrast {
    color: $ske-light-primary-button-color-contrast;
  }

  .ske-color-text--secondary {
    color: $ske-light-secondary-color;
  }

  .ske-color-text--secondary-contrast {
    color: $ske-light-secondary-color-contrast;
  }

  .ske-color-text--imagearea {
    color: $ske-light-imagearea-color;
  }

  .ske-color-text--imagearea-contrast {
    color: $ske-light-imagearea-color-contrast;
  }

  .ske-color-text--error {
    color: $ske-light-error-color;
  }

  .ske-color-text--error-contrast {
    color: $ske-light-error-color-contrast;
  }

  .ske-color-text--main {
    color: $ske-light-text-color-primary;
  }

  .ske-color-text--alternative {
    color: $ske-light-text-color-secondary;
  }

  .ske-color-text--disabled {
    color: $ske-light-text-color-disabled;
  }

}

// mdl/src/typography/_typography.scss
// -----------------------------------
.mdlext-light-color-theme {
  a {
    color: $ske-light-text-link-color;
  }
}



