// -----------------------------------------------------------------------------
// This file contains all styles related to the footer of the site/application.
// -----------------------------------------------------------------------------

.ske-bunn {
  border-radius: $ske-bunn-border-radius-sm;
  margin: $ske-bunn-margin-sm;
  padding: $ske-bunn-padding-sm;
  height: $ske-bunn-height-sm;
  flex: none;
  display: flex;
  align-items: center;
  background-clip: padding-box; // Rounded corners: Prevent background color leak outs
  overflow: hidden; // Prevent child elements background color leak outs

  @include respond-to('tablet') {
    border-radius: $ske-bunn-border-radius-lg;
    margin: $ske-bunn-margin-sm;
    padding: $ske-bunn-padding-lg;
    height: $ske-bunn-height-lg;
  }
}




