// ----------------------------------------------------------------
// Variables related to Layout, Header, Footer
// ----------------------------------------------------------------

/// Layout
$ske-layout-kjerne-border-radius-sm: $ske-default-border-radius-sm !default;  // top left, top right, bottom right, bottom left
$ske-layout-kjerne-border-radius-lg: $ske-default-border-radius-lg !default;
$ske-layout-kjerne-margin-sm: $ske-default-margin-sm !default;
$ske-layout-kjerne-margin-lg: $ske-default-margin-lg !default;
$ske-layout-kjerne-padding-sm: $ske-default-padding-sm !default;
$ske-layout-kjerne-padding-lg: $ske-default-padding-lg !default;
$ske-layout-venstre-width: 300px !default;
$ske-layout-venstre-border-radius-sm: 0 $ske-default-border-radius-sm $ske-default-border-radius-sm 0 !default;  // top left, top right, bottom right, bottom left
$ske-layout-venstre-border-radius-lg: 0 $ske-default-border-radius-lg $ske-default-border-radius-lg 0 !default;
$ske-layout-venstre-margin-sm: $ske-default-margin-sm $ske-default-margin-sm 0 $ske-default-margin-sm !default;
$ske-layout-venstre-margin-lg: $ske-default-margin-lg 0 0 !default;
$ske-layout-venstre-padding-sm: $ske-default-padding-sm !default;
$ske-layout-venstre-padding-lg: $ske-default-padding-lg !default;
$ske-layout-hoeyre-width: 260px !default;
$ske-layout-hoeyre-border-radius-sm: $ske-default-border-radius-sm 0 0 $ske-default-border-radius-sm !default;  // top left, top right, bottom right, bottom left
$ske-layout-hoeyre-border-radius-lg: $ske-default-border-radius-lg 0 0 $ske-default-border-radius-lg !default;
$ske-layout-hoeyre-margin-sm: 0 $ske-default-margin-sm $ske-default-margin-sm $ske-default-margin-sm !default;
$ske-layout-hoeyre-margin-lg: $ske-default-margin-lg 0 0 !default;
$ske-layout-hoeyre-padding-sm: $ske-default-padding-sm !default;
$ske-layout-hoeyre-padding-lg: $ske-default-padding-lg !default;


// Topp - header
$ske-topp-border-radius-sm: 0 0 0 0 !default;  // top left, top right, bottom right, bottom left
$ske-topp-border-radius-lg: 0 0 0 0 !default;
$ske-topp-background: rgba(#ECE6E8, 1) !default; // <- Eksternt: _variables-extern.scss
$ske-topp-font-color: rgba(#000000, 1) !default; // <- Eksternt: _variables-extern.scss
$ske-topp-height-sm: 112px !default;
$ske-topp-height-lg: 136px !default;
$ske-topp-margin-sm: 0 !default;
$ske-topp-margin-lg: 0 !default;
$ske-topp-logo-height-sm: $ske-topp-height-sm - $ske-default-padding-sm*2 !default;
$ske-topp-logo-height-lg: $ske-topp-height-lg - $ske-default-padding-lg*2 !default;
$ske-topp-logo-url: $ske-image-path-svg + "/ske-logo-intern.svg" !default; // <- Eksternt: _variables-extern.scss
$ske-topp-logo-background: url($ske-topp-logo-url) no-repeat center !default;
$ske-topp-venstre-seksjon-background: rgba($ske-color-primary, 1) !default; // <- Eksternt: _variables-extern.scss
$ske-topp-venstre-seksjon-width-sm: 250px !default;
$ske-topp-venstre-seksjon-width-lg: 320px !default;
$ske-topp-venstre-seksjon-padding-sm: 0 0 0 $ske-default-padding-sm !default;
$ske-topp-venstre-seksjon-padding-lg: 0 0 0 $ske-default-padding-lg !default;
$ske-topp-diagonal-width-sm: 40px !default;
$ske-topp-diagonal-width-lg: 48px !default;
$ske-topp-hoeyre-seksjon-color: $ske-topp-background !default;
$ske-topp-hoeyre-seksjon-padding-sm: 0 $ske-default-padding-sm !default;
$ske-topp-hoeyre-seksjon-padding-lg: 0 $ske-default-padding-lg !default;

// Bunn - footer
$ske-bunn-border-radius-sm: $ske-default-border-radius-sm $ske-default-border-radius-sm 0 0 !default;  // top left, top right, bottom right, bottom left
$ske-bunn-border-radius-lg: $ske-default-border-radius-lg $ske-default-border-radius-lg 0 0 !default;
$ske-bunn-height-sm: 56px !default;
$ske-bunn-height-lg: 72px !default;
$ske-bunn-margin-sm: 0 !default;
$ske-bunn-margin-lg: 0 !default;
$ske-bunn-padding-sm: 0 $ske-default-padding-sm !default;
$ske-bunn-padding-lg: 0 $ske-default-padding-lg !default;





