// Legges i variables

// Dark
$ske-dark-primary-button-color: #77C2B7;
$ske-dark-primary-button-color-contrast: #000000;


.mdlext-dark-color-theme {
  background-color: $ske-dark-content-background-color;
  color: $ske-dark-text-color-primary;
}

// mdl/src/palette/_palette.scss
.mdlext-dark-color-theme {

  // Material Design Lite

  .mdl-color--primary {
    background-color: $ske-dark-color-primary !important;
  }

  .mdl-color--primary-contrast {
    background-color: $ske-dark-color-primary-contrast !important;
  }

  .mdl-color--primary-dark {
    background-color: $ske-dark-color-primary-dark !important;
  }

  .mdl-color--accent {
    background-color: $ske-dark-color-accent !important;
  }

  .mdl-color--accent-contrast {
    background-color: $ske-dark-color-accent-contrast !important;
  }

  .ske-color-text--primary-button {
    color: $ske-dark-primary-button-color;
  }

  .ske-color-text--primary-button-contrast {
    color: $ske-dark-primary-button-color-contrast;
  }

  .mdl-color-text--primary {
    color: $ske-dark-color-primary !important;
  }

  .mdl-color-text--primary-contrast {
    color: $ske-dark-color-primary-contrast !important;
  }

  .mdl-color-text--primary-dark {
    color: $ske-dark-color-primary-dark !important;
  }

  .mdl-color-text--accent {
    color: $ske-dark-color-accent !important;
  }

  .mdl-color-text--accent-contrast {
    color: $ske-dark-color-accent-contrast !important;
  }


  // SKE

  .ske-color--primary-button {
    background-color: $ske-dark-primary-button-color;
  }

  .ske-color--primary-button-contrast {
    background-color: $ske-dark-primary-button-color-contrast;
  }

  .ske-color--secondary {
    background-color: $ske-dark-secondary-color;
  }

  .ske-color--secondary-contrast {
    background-color: $ske-dark-secondary-color-contrast;
  }

  .ske-color--imagearea {
    background-color: $ske-dark-imagearea-color;
  }

  .ske-color--imagearea-contrast {
    background-color: $ske-dark-imagearea-color-contrast;
  }

  .ske-color--error {
    background-color: $ske-dark-error-color;
  }

  .ske-color--error-contrast {
    background-color: $ske-dark-error-color-contrast;
  }

  .ske-color-text--secondary {
    color: $ske-dark-secondary-color;
  }

  .ske-color-text--secondary-contrast {
    color: $ske-dark-secondary-color-contrast;
  }

  .ske-color-text--imagearea {
    color: $ske-dark-imagearea-color;
  }

  .ske-color-text--imagearea-contrast {
    color: $ske-dark-imagearea-color-contrast;
  }

  .ske-color-text--error {
    color: $ske-dark-error-color;
  }

  .ske-color-text--error-contrast {
    color: $ske-dark-error-color-contrast;
  }

  .ske-color-text--main {
    color: $ske-dark-text-color-primary;
  }

  .ske-color-text--alternative {
    color: $ske-dark-text-color-secondary;
  }

  .ske-color-text--disabled {
    color: $ske-dark-text-color-disabled;
  }

}

// mdl/src/typography/_typography.scss
.mdlext-dark-color-theme {
  a {
    color: $ske-dark-text-link-color;
  }
}




