// Lys
.mdlext-light-color-theme {
  a:visited {
    color: $ske-light-text-link-visited-color;
  }
  .ske-error-color-text {
    color: $ske-light-error-color;
  }
  .ske-error-banner {
    background-color: $ske-light-error-color;
    color: $ske-light-error-color-contrast;
  }
  .ske-graphics-color {
    background-color: $ske-light-graphics-color;
  }
  .ske-dialog-color {
    background-color: $ske-light-messagebox-color;
  }
}

// MÃ¸rk
.mdlext-dark-color-theme {
  a:visited {
    color: $ske-dark-text-link-visited-color;
  }
  a:focus,
  a:active {
    outline-color: $mdlext-dark-text-color-secondary; // TODO
  }
  .ske-error-color-text {
    color: $ske-dark-error-color;
  }
  .ske-error-banner {
    background-color: $ske-dark-error-color;
    color: $ske-dark-error-color-contrast;
  }
  .ske-graphics-color {
    background-color: $ske-dark-graphics-color;
  }
  .ske-dialog-color {
    background-color: $ske-dark-messagebox-color;
  }
}




