/* Variables */
$ske-button-bottom-line-height: 3px;

/* Nedtonet knapp */
$ske-button-nedtonet-color: #666666;
$ske-button-nedtonet-hover-color: $ske-color-accent;
$ske-button-nedtonet-active-color: rgba(158, 158, 158, 0.4);

$ske-dark-button-nedtonet-color: #e6e6e6;
$ske-dark-button-nedtonet-hover-color: $ske-dark-color-accent;
$ske-dark-button-nedtonet-active-color: rgba(0, 0, 0, 0.3);

// The button component. Defaults to a flat button.
.mdl-button {
  font-weight: 600;
  min-width: 20px;
  text-decoration: underline;
}

// Raised buttons
.mdl-button--raised {
  text-decoration: none;
}

// Primary button
.mdl-button--primary.mdl-button--primary {
  .material-icons {
    padding-bottom: 2px;
  }

  &.mdl-button--raised {
    border-bottom: $ske-button-bottom-line-height solid darken($ske-button-primary-color-alt, 5%);
    box-shadow: none;
    .material-icons {
      padding-bottom: $ske-button-bottom-line-height;
    }
  }
  &.mdl-button--raised,
  &.mdl-button--fab {
    &:hover {
      @include shadow-4dp();
    }
    &:active {
      @include shadow-6dp();
    }
    &:focus:not(:active) {
      @include shadow-4dp();
    }
  }
}

// Accent button
.mdl-button--accent.mdl-button--accent {
  text-decoration: underline;
  &:active {
    text-decoration: none;
  }
  &.mdl-button--raised {
    border-bottom: $ske-button-bottom-line-height solid darken($ske-light-color-accent, 5%);
    box-shadow: none;
    text-decoration: none;
  }
  &.mdl-button--raised,
  &.mdl-button--fab {
    &:hover {
      @include shadow-4dp();
    }
    &:active {
      @include shadow-6dp();
    }
  }
}

// Nedtonet knapp
.ske-button--nedtonet.ske-button--nedtonet {
  text-decoration: underline;
  color: $ske-button-nedtonet-color;
  &:hover {
    color: $ske-button-nedtonet-hover-color;
  }
  &:active {
    background-color: $ske-button-nedtonet-active-color;
    text-decoration: none;
  }

  &.mdl-button--raised {
    border-bottom: $ske-button-bottom-line-height solid lighten($ske-button-nedtonet-color, 40%);
    box-shadow: none;
    text-decoration: none;
  }
  &.mdl-button--raised,
  &.mdl-button--fab {
    &:hover {
      @include shadow-4dp();
    }
    &:active {
      @include shadow-6dp();
    }
  }
}


// Overstyring av fargetema

/* LIGHT COLOR THEME */
.mdlext-light-color-theme {

  // Colorized buttons
  .mdl-button--primary.mdl-button--primary {
    &.mdl-button--raised {
      background-color: $ske-light-button-primary-color-alt;
      border-bottom-color: darken($ske-light-button-primary-color-alt, 5%);
    }
  }

  .mdl-button--accent.mdl-button--accent {
    &.mdl-button--raised {
      border-bottom-color: darken($ske-light-color-accent, 5%);
    }
  }
}

/* DARK COLOR THEME */
.mdlext-dark-color-theme {

  // Colorized buttons
  .mdl-button--primary.mdl-button--primary {
    &.mdl-button--raised {
      background-color: $ske-dark-button-primary-color-alt;
      border-bottom-color: $ske-dark-button-primary-color-dark-alt;
    }
  }

  .mdl-button--accent.mdl-button--accent {
    &.mdl-button--raised {
      border-bottom-color: darken($ske-dark-color-accent, 40%);

      &:hover {
        background-color: darken($ske-dark-color-accent, 10%);
        border-bottom-color: darken($ske-dark-color-accent, 45%);
      }

      &:active {
        background-color: darken($ske-dark-color-accent, 10%);
        border-bottom-color: darken($ske-dark-color-accent, 45%);
      }

    }
    &.mdl-button--fab {
      &:hover {
        border-bottom-color: darken($ske-dark-color-accent, 10%);
      }
    }
  }

  // Nedtonet knapp
  .ske-button--nedtonet.ske-button--nedtonet {
    color: $ske-dark-button-nedtonet-color;
    &:hover {
      color: $ske-dark-button-nedtonet-hover-color;
    }
    &:active {
      background-color: $ske-dark-button-nedtonet-active-color;
    }
    &.mdl-button--raised {
      border-bottom: $ske-button-bottom-line-height solid darken($ske-dark-button-nedtonet-color, 40%);
      box-shadow: none;
      text-decoration: none;
    }
    &.mdl-button--raised,
    &.mdl-button--fab {
      &:hover {
        @include shadow-4dp();
      }
      &:active {
        @include shadow-6dp();
      }
    }
  }
}



