@charset "UTF-8";


/**
// Responsive Tables
// Based on:
//   http://dbushell.com/2016/03/04/css-only-responsive-tables/
*/

.ske-table {

  // IE needs inline-block to position scrolling shadows otherwise use:
  // display: block;
  // max-width: min-content;
  display: inline-block;
  max-width: 100%;

  vertical-align: top;
  overflow-x: auto;
  border-collapse: collapse;
  border-spacing: 0;
  font-size: $data-table-font-size;

  cursor: default;

  -webkit-overflow-scrolling: touch; // optional - enable iOS momentum scrolling
  white-space: nowrap;               // optional - looks better for small cell values

  th,
  td {
    position: relative;
    padding: 8px $data-table-column-padding 8px $data-table-column-padding;
    border-top: $data-table-dividers;
    @include typo-body-1();
  }

  th {
    text-transform: uppercase;
    text-align: left;
    text-overflow: ellipsis;
    border-top: 0;
    font-weight: 600;
    vertical-align: bottom;
  }

  tbody > tr {

    &[hidden=''],
    &[hidden=true] {
      display: none;
    }

    &.is-selected {
      background-color: rgba($data-table-selection-color, 0.8);
    }

    &:hover {
      background-color: rgba($data-table-hover-color, 0.5);
    }
  }

  .ske-table__align-right.ske-table__align-left {
    text-align: left;
  }

  .ske-table__align-right.ske-table__align-right {
    text-align: right;
  }

  .ske-table__align-center.ske-table__align-center {
    text-align: center;
  }
}

//// Group / Sub row
.ske-table {

  tr:not(&__summary-row) {
    // i.e. __detail-row
    td {
      border-top: 0;
      padding-top: 4px;
      padding-bottom: 4px;
    }

    table {
      max-width: 100%;
      white-space: nowrap;
      border-collapse: collapse;
      border-spacing: 0;
      border: 0;
      font-size: $data-table-font-size;

      th,
      td {
        //border: 0;
      }
    }

  }
}

// Toggle
.ske-table-toggle__icon::after {
  content: 'expand_more';
}

[aria-expanded=true] .ske-table-toggle .ske-table-toggle__icon::after {
  content: 'expand_less';
}



