// -----------------------------------------------------------------------------
// This file contains all styles related to the layout of the site/application.
// -----------------------------------------------------------------------------
//* Based on: https://github.com/philipwalton/solved-by-flexbox/blob/master/assets/css/components/holy-grail.css
//* 1. Avoid the IE 10-11 `min-height` bug.
//* 2. Set `flex-shrink` to `0` to prevent some browsers from
//*    letting these items shrink to smaller than their content's default
//*    minimum size. See http://bit.ly/1Mn35US for details.
//* 3. Use `%` instead of `vh` since `vh` is buggy in older mobile Safari.

.ske-layout {
  //background-color: yellow; // trace
  display: flex;
  min-height: 100vh;
  height: 100%; /* 1, 3 */
  flex-direction: column;

  &__body {
    //background-color: cyan; // trace
    display: flex;
    flex: 1 0 auto; /* 2 */
    flex-direction: column;
  }

  &__wrapper {
    //background-color: orange; // trace
    display: flex;
    flex-direction: column;
    flex: 1 0 auto; /* 2 */
  }

  &__kjerne {
    border-radius: $ske-layout-kjerne-border-radius-sm;
    margin: $ske-layout-kjerne-margin-sm;
    padding: $ske-layout-kjerne-padding-sm;
    flex: 1;
    background-clip: padding-box; // Rounded corners: Prevent background color leak outs
    overflow: hidden; // Prevent child elements background color leak outs
  }

  &__venstre,
  &__hoeyre {
    display: flex;
    flex-direction: column;
    background-clip: padding-box; // Rounded corners: Prevent background color leak outs
    overflow: hidden; // Prevent child elements background color leak outs
  }

  &__venstre {
    margin: $ske-layout-venstre-margin-sm;
    padding: $ske-layout-venstre-padding-sm;
    border-radius: $ske-layout-venstre-border-radius-sm;
    order: -1;
  }

  &__hoeyre {
    margin: $ske-layout-hoeyre-margin-sm;
    padding: $ske-layout-hoeyre-padding-sm;
    border-radius: $ske-layout-hoeyre-border-radius-sm;
  }

  @include respond-to('tablet') {
    &__body {
      flex-direction: row;
    }

    &__kjerne {
      border-radius: $ske-layout-kjerne-border-radius-lg;
      margin: $ske-layout-kjerne-margin-lg;
      padding: $ske-layout-kjerne-padding-lg;
    }

    &__venstre {
      margin: $ske-layout-venstre-margin-lg;
      padding: $ske-layout-venstre-padding-lg;
      border-radius: $ske-layout-venstre-border-radius-lg;
      flex: 0 0 $ske-layout-venstre-width;
    }

    &__hoeyre {
      margin: $ske-layout-hoeyre-margin-lg;
      padding: $ske-layout-hoeyre-padding-lg;
      border-radius: $ske-layout-hoeyre-border-radius-lg;
      flex: 0 0 0;
    }
  }

  @include respond-to('desktop') {
    &__hoeyre {
      flex: 0 0 $ske-layout-hoeyre-width;
    }
  }
}



