// Legges i variables

// Default
$ske-primary-button-color: #328279;
$ske-primary-button-color-contrast: #FFFFFF;


// SKE

.ske-color--primary-button {
  background-color: $ske-primary-button-color;
}

.ske-color--primary-button-contrast {
  background-color: $ske-primary-button-color-contrast;
}

.ske-color--secondary {
  background-color: $ske-secondary-color;
}

.ske-color--secondary-contrast {
  background-color: $ske-secondary-color-contrast;
}

.ske-color--imagearea {
  background-color: $ske-imagearea-color;
}

.ske-color--imagearea-contrast {
  background-color: $ske-imagearea-color-contrast;
}

.ske-color--error {
  background-color: $ske-error-color;
}

.ske-color--error-contrast {
  background-color: $ske-error-color-contrast;
}

.ske-color-text--primary-button {
  color: $ske-primary-button-color;
}

.ske-color-text--primary-button-contrast {
  color: $ske-primary-button-color-contrast;
}

.ske-color-text--secondary {
  color: $ske-secondary-color;
}

.ske-color-text--secondary-contrast {
  color: $ske-secondary-color-contrast;
}

.ske-color-text--imagearea {
  color: $ske-imagearea-color;
}

.ske-color-text--imagearea-contrast {
  color: $ske-imagearea-color-contrast;
}

.ske-color-text--error {
  color: $ske-error-color;
}

.ske-color-text--error-contrast {
  color: $ske-error-color-contrast;
}

.ske-color-text--main {
  color: $ske-text-color-primary;
}

.ske-color-text--alternative {
  color: $ske-text-color-secondary;
}

.ske-color-text--disabled {
  color: $ske-text-color-disabled;
}




