// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------

.ske-topp {
  background: $ske-topp-background;
  color: $ske-topp-font-color;
  height: $ske-topp-height-sm;
  margin: $ske-topp-margin-sm;
  border-radius: $ske-topp-border-radius-sm;
  flex: none;
  display: flex;
  align-items: stretch;
  background-clip: padding-box; // Rounded corners: Prevent background color leak outs
  overflow: hidden; // Prevent child elements background color leak outs

  &__rad {
    display: flex;
    flex: 1 100%;
    overflow: hidden;
  }

  &__logo {
    width: $ske-topp-logo-height-sm;
    height: $ske-topp-logo-height-sm;
    background: $ske-topp-logo-background;
    background-size: contain;
  }

  &__venstre-seksjon {
    background: $ske-topp-venstre-seksjon-background;
    padding: $ske-topp-venstre-seksjon-padding-sm;
    width: $ske-topp-venstre-seksjon-width-sm - $ske-topp-diagonal-width-sm;
    display: flex;
    align-items: center;
  }

  &__diagonal {
    border-right: $ske-topp-diagonal-width-sm solid $ske-topp-hoeyre-seksjon-color;
    border-bottom: $ske-topp-height-sm solid $ske-topp-venstre-seksjon-background;
  }

  &__hoeyre-seksjon {
    background: $ske-topp-hoeyre-seksjon-color;
    padding: $ske-topp-hoeyre-seksjon-padding-sm;
    flex-grow: 1;
    display: flex;
    align-items: center;
  }

  @include respond-to('tablet') {
    height: $ske-topp-height-lg;
    margin: $ske-topp-margin-lg;
    border-radius: $ske-topp-border-radius-lg;

    &__logo {
      width: $ske-topp-logo-height-lg;
      height: $ske-topp-logo-height-lg;
    }

    &__venstre-seksjon {
      padding: $ske-topp-venstre-seksjon-padding-sm;
      width: $ske-topp-venstre-seksjon-width-lg - $ske-topp-diagonal-width-lg;
    }

    &__diagonal {
      border-right-width: $ske-topp-diagonal-width-lg;
      border-bottom-width: $ske-topp-height-lg;
    }

    &__hoeyre-seksjon {
      padding: $ske-topp-hoeyre-seksjon-padding-sm;
    }
  }
}



